import React, {useState, useEffect} from "react";
import '../../App.css';
import '../../style/Auth.css'
import * as auth from "../../apis/auth";
import EmailTimer from "./utils/EmailTimer";
import SocialLoginBtns from "./utils/SocialLoginBtns";
import InputBox from "./utils/InputBox";
import {emailRegex, passwordRegex} from "./utils/Regex";

function SignUp({setIsLoading}) {
    const [username, setUsername] = useState("");
    const [usernameMessage, setUsernameMessage] = useState("");
    const [startTimer, setStartTimer] = useState(false);
    const [authNumber, setAuthNumber] = useState("");
    const [authNumberMessage, setAuthNumberMessage] = useState("");
    const [authEmail, setAuthEmail] = useState(false);
    const [password, setPassword] = useState("");
    const [passwordMessage, setPasswordMessage] = useState("");
    const [equalPassword, setEqualPassword] = useState("")
    const [equalPasswordMessage, setEqualPasswordMessage] = useState("");

    useEffect(() => {
        const isLogin = window.localStorage.getItem("username");
        if (isLogin) {
            window.location.href = "/";
        }

    }, []);

    function setMessage() {
        setUsernameMessage("");
        setAuthNumberMessage("");
        setPasswordMessage("");
        setEqualPasswordMessage("");
    }

    //  
    const submitMail = async () => {
        setMessage();
        if (!username) {
            setUsernameMessage(" .");
            return;
        }
        if (!emailRegex(username)) {
            setUsernameMessage("  .");
            return;
        }

        try {
            setIsLoading(true);
            const response = await auth.submitMail(username);
            if (response.success) {
                setStartTimer(true);
            }
        } catch (error) {
            setUsernameMessage(error.errorMessage);
        } finally {
            setIsLoading(false);
        }
    }

    const authMail = async () => {
        setMessage();
        if (!authNumber || isNaN(authNumber)) {
            setAuthNumberMessage("  .");
            return;
        }

        try {
            setIsLoading(true);
            const response = await auth.authMail(username, authNumber);
            if (response.success) {
                setStartTimer(false);
                const usernameInput = document.getElementById("email-input-box");
                usernameInput.readOnly = true;
                const authEmailInput = document.getElementById("email-auth-input-box");
                authEmailInput.readOnly = true;
                setAuthEmail(true);
            } else {
                setAuthNumberMessage(response.message);
            }
        } catch (error) {
            setAuthNumberMessage(error.errorMessage);
        } finally {
            setIsLoading(false);
        }
    }

    const signUp = async () => {
        setMessage();
        if (!username || !authNumber || !password || !equalPassword) {
            if (!username) {
                setUsernameMessage(" .");
            }
            if (!password) {
                setPasswordMessage(" .");
            }
            if (!authNumber) {
                setAuthNumberMessage(" .");
            }
            if (!equalPassword) {
                setEqualPasswordMessage("  .");
            }
            return;
        }

        if (!emailRegex(username)) {
            setUsernameMessage("   .");
            return;
        }

        if (!passwordRegex(password)) {
            setPasswordMessage(" 8~10 ,   .");
            return;
        }

        if (password !== equalPassword) {
            setPasswordMessage("  .");
            setEqualPasswordMessage("  .");
            return;
        }

        if (!authEmail) {
            setUsernameMessage("   .")
            setAuthNumberMessage("   .");
            return;
        }

        try {
            const response = await auth.signup(username, authNumber, password, equalPassword);
            if (response.success) {
                alert("  .");
                window.location.href = "/login";
            }
        } catch (error) {
            setPasswordMessage("    .");
            setUsernameMessage("    .");
        }
    }

    return (
        <>
            <div className="auth-container">
                <div className="mention">
                    <p> .</p>
                </div>
                <div className="signup-wrap">
                    <div className="input-btn-wrap">
                        <div className="input-btn">
                            <InputBox
                                className={"login"}
                                type={"email"}
                                id={"email-input-box"}
                                placeholder={""}
                                value={username}
                                setValue={setUsername}
                                onKeyPress={submitMail}
                                message={usernameMessage}
                            />
                            <button className="email-submit-btn" onClick={submitMail}></button>
                        </div>
                        <EmailTimer startTimer={startTimer}/>
                    </div>
                    <div className="input-btn-wrap">
                        <div className="input-btn">
                            <InputBox
                                className={"email_auth"}
                                type={"text"}
                                id={"email-auth-input-box"}
                                placeholder={"  ()"}
                                value={authNumber}
                                setValue={setAuthNumber}
                                onKeyPress={authMail}
                                message={authNumberMessage}
                            />
                            <button className="email-auth-btn" onClick={authMail}></button>
                        </div>
                    </div>
                    <InputBox
                        className={"password"}
                        type={"password"}
                        id={"password-input-box"}
                        placeholder={" (8~20 , )"}
                        value={password}
                        setValue={setPassword}
                        onKeyPress={signUp}
                        message={passwordMessage}
                    />
                    <InputBox
                        className={"password"}
                        type={"password"}
                        id={"password-equal-input-box"}
                        placeholder={" "}
                        value={equalPassword}
                        setValue={setEqualPassword}
                        onKeyPress={signUp}
                        message={equalPasswordMessage}
                    />
                    <button className="login-btn" onClick={signUp}></button>
                    <div className="link-wrap">
                        <a className="signup" href="./login"></a>
                    </div>
                </div>
                <div className="bar"></div>
                <SocialLoginBtns/>
            </div>
        </>
    );
}

export default SignUp;
