import React from "react";
import { call } from "../../service/api-service";
import SearchIcon from "@mui/icons-material/Search";

const FriendCubeWrap = (props) => {
    const addCubeTicket = async (characterId, authority) => {
        if (!authority) {
            props.showMessage(" .");
            return;
        }
        props.setShowLinearProgress(true);
        const updatedCharacters = props.characters.map((character) => {
            if (character.id === characterId) {
                var updatedCharacter = {
                    ...character
                };
                const updateContent = {
                    id: character.id,
                    characterName: character.characterName,
                };
                return call("/v2/friends/cube/add", "PATCH", updateContent)
                    .then((response) => {
                        props.setShowLinearProgress(false);
                        updatedCharacter = response;
                        return updatedCharacter;
                    })
                    .catch((error) => {
                        props.setShowLinearProgress(false);
                        alert(error.errorMessage);
                        return updatedCharacter;
                    });
            }
            return character;
        });
        props.setCharacters(await Promise.all(updatedCharacters));
    };

    const substractCubeTicket = async (characterId, authority) => {
        if (!authority) {
            props.showMessage(" .");
            return;
        }
        props.setShowLinearProgress(true);
        const updatedCharacters = props.characters.map((character) => {
            if (character.id === characterId) {
                var updatedCharacter = {
                    ...character
                };
                const updateContent = {
                    id: character.id,
                    characterName: character.characterName,
                };
                return call("/v2/friends/cube/substract", "PATCH", updateContent)
                    .then((response) => {
                        props.setShowLinearProgress(false);
                        updatedCharacter = response;
                        return updatedCharacter;
                    })
                    .catch((error) => {
                        props.setShowLinearProgress(false);
                        alert(error.errorMessage);
                        return updatedCharacter;
                    });
            }
            return character;
        });
        props.setCharacters(await Promise.all(updatedCharacters));
    };

    const getCubeContent = async (itemLevel) => {
        props.setShowLinearProgress(true);
        var name;
        if (itemLevel < 1490.0) {
            name = "1";
        } else if (itemLevel >= 1490.0 && itemLevel < 1540.0) {
            name = "2";
        } else if (itemLevel >= 1540.0 && itemLevel < 1580.0) {
            name = "3";
        } else if (itemLevel >= 1580.0 && itemLevel < 1610.0) {
            name = "4";
        } else {
            name = "5";
        }
        try {
            props.setShowLinearProgress(false);
            return await call("/v2/character/cube/" + name, "GET", null);
        } catch (error) {
            props.setShowLinearProgress(false);
            alert(error.errorMessage);
        }
    };

    const openCubeAverage = async (itemLevel) => {
        props.setModalTitle("   ");
        const cubeContent = await getCubeContent(itemLevel);
        var modalContent = (
            <div className="chaosVisual">
                <p>
                    <button
                        className="prev"
                        style={{ cursor: "pointer", marginRight: 5 }}
                        onClick={() => previousCubeContent(cubeContent.name)}
                    >
                        
                    </button>
                      <strong>{cubeContent.name}</strong>
                    <button
                        className="next"
                        style={{ cursor: "pointer", marginLeft: 5 }}
                        onClick={() => nextCubeContent(cubeContent.name)}
                    >
                        
                    </button>
                </p>
                <div className="flex" style={{ alignItems: "flex-start" }}>
                    <ul>
                        <strong>  </strong>
                        <li>1 <em>{cubeContent.jewelry}</em></li>
                        <li> <em> {cubeContent.jewelryPrice} G</em></li>
                        <li>  <em>{cubeContent.jewelry * cubeContent.jewelryPrice} G</em></li>
                    </ul>
                    <ul>
                        <strong>  </strong>
                        <li> <em>{cubeContent.leapStone}</em></li>
                        <li> <em>{cubeContent.shilling}</em></li>
                        <li> <em>{cubeContent.solarGrace}</em></li>
                        <li> <em>{cubeContent.solarBlessing}</em></li>
                        <li> <em>{cubeContent.solarProtection}</em></li>
                        <li> <em>{cubeContent.cardExp}</em></li>
                    </ul>
                </div>
            </div>
        );
        props.setModalContent(modalContent);
        props.setOpenModal(true);

    };

    const previousCubeContent = async (name) => {
        props.setShowLinearProgress(true);
        const cubeContentList = ["1", "2", "3", "4", "5"];
        const currentIndex = cubeContentList.indexOf(name);
        const previousIndex = currentIndex === 0 ? cubeContentList.length - 1 : currentIndex - 1;
        const preName = cubeContentList[previousIndex];

        try {
            props.setShowLinearProgress(false);
            const cubeContent = await call("/v2/character/cube/" + preName, "GET", null);
            var modalContent = (
                <div className="chaosVisual">
                    <p>
                        <button
                            className="prev"
                            style={{ cursor: "pointer", marginRight: 5 }}
                            onClick={() => previousCubeContent(cubeContent.name)}
                        >
                            
                        </button>
                          <strong>{cubeContent.name}</strong>
                        <button
                            className="next"
                            style={{ cursor: "pointer", marginLeft: 5 }}
                            onClick={() => nextCubeContent(cubeContent.name)}
                        >
                            
                        </button>
                    </p>
                    <div className="flex" style={{ alignItems: "flex-start" }}>
                        <ul>
                            <strong>  </strong>
                            <li>1 <em>{cubeContent.jewelry}</em></li>
                            <li> <em> {cubeContent.jewelryPrice} G</em></li>
                            <li>  <em>{cubeContent.jewelry * cubeContent.jewelryPrice} G</em></li>
                        </ul>
                        <ul>
                            <strong>  </strong>
                            <li> <em>{cubeContent.leapStone}</em></li>
                            <li> <em>{cubeContent.shilling}</em></li>
                            <li> <em>{cubeContent.solarGrace}</em></li>
                            <li> <em>{cubeContent.solarBlessing}</em></li>
                            <li> <em>{cubeContent.solarProtection}</em></li>
                            <li> <em>{cubeContent.cardExp}</em></li>
                        </ul>
                    </div>
                </div>
            );
            props.setModalContent(modalContent);
        } catch (error) {
            props.setShowLinearProgress(false);
            alert(error.errorMessage);
        }
    };

    const nextCubeContent = async (name) => {
        props.setShowLinearProgress(true);
        const cubeContentList = ["1", "2", "3", "4", "5"];

        const currentIndex = cubeContentList.indexOf(name);
        const nextIndex = currentIndex === cubeContentList.length - 1 ? 0 : currentIndex + 1;
        const nextName = cubeContentList[nextIndex];

        try {
            props.setShowLinearProgress(false);
            const cubeContent = await call("/v2/character/cube/" + nextName, "GET", null);
            var modalContent = (
                <div className="chaosVisual">
                    <p>
                        <button
                            className="prev"
                            style={{ cursor: "pointer", marginRight: 5 }}
                            onClick={() => previousCubeContent(cubeContent.name)}
                        >
                            
                        </button>
                          <strong>{cubeContent.name}</strong>
                        <button
                            className="next"
                            style={{ cursor: "pointer", marginLeft: 5 }}
                            onClick={() => nextCubeContent(cubeContent.name)}
                        >
                            
                        </button>
                    </p>
                    <div className="flex" style={{ alignItems: "flex-start" }}>
                        <ul>
                            <strong>  </strong>
                            <li>1 <em>{cubeContent.jewelry}</em></li>
                            <li> <em> {cubeContent.jewelryPrice} G</em></li>
                            <li>  <em>{cubeContent.jewelry * cubeContent.jewelryPrice} G</em></li>
                        </ul>
                        <ul>
                            <strong>  </strong>
                            <li> <em>{cubeContent.leapStone}</em></li>
                            <li> <em>{cubeContent.shilling}</em></li>
                            <li> <em>{cubeContent.solarGrace}</em></li>
                            <li> <em>{cubeContent.solarBlessing}</em></li>
                            <li> <em>{cubeContent.solarProtection}</em></li>
                            <li> <em>{cubeContent.cardExp}</em></li>
                        </ul>
                    </div>
                </div>
            );
            props.setModalContent(modalContent);
        } catch (error) {
            props.setShowLinearProgress(false);
            alert(error.errorMessage);
        }
    };

    return (
        <div className="content-wrap">
            <div
                className="content"
                style={{
                    height: 35,
                    position: "relative",
                    justifyContent: "space-between",
                    fontSize: 14,
                }}
            >
                <div style={{ display: "flex", flexDirection: "row", justifyContent: "center" }}>
                    <button
                        className ="minus"
                        style={{ cursor: "pointer" }}
                        onClick={() => substractCubeTicket(props.character.id, props.friendSetting.checkWeekTodo)}
                    >
                        -
                    </button>
                    <div
                        style={{ width: "90px", justifyContent: "center", alignItems: "center", display: "flex" }}
                    >
                        {props.character.cubeTicket} 
                    </div>
                    <button
                        className ="plus"
                        style={{ cursor: "pointer" }}
                        onClick={() => addCubeTicket(props.character.id, props.friendSetting.checkWeekTodo)}
                    >
                        +
                    </button>
                    <div
                        style={{ width: "100%", marginLeft: 10 }}
                    >
                         
                    </div>
                    <SearchIcon onClick={() => openCubeAverage(props.character.itemLevel)} style={{ cursor: "pointer" }} />
                </div>
            </div>
        </div>
    );
};

export default FriendCubeWrap;