import React from 'react';
import CommentInsertForm from './CommentInsertForm';

const Comment = ({
    comment,
    replies,
    setActiveComment,
    activeComment,
    updateComment,
    deleteComment,
    addComment,
    parentId = null,
    currentUser, 
    currentPage
  }) => {
    const isEditing =
      activeComment &&
      activeComment.id === comment.id &&
      activeComment.type === "editing";
    const isReplying =
      activeComment &&
      activeComment.id === comment.id &&
      activeComment.type === "replying";
    const canDelete =
      (currentUser && currentUser.id === comment.memberId && replies.length === 0);
    const canReply = currentUser && ((currentUser.role === "ADMIN" || "PUBLISHER") || (currentUser.id === comment.memberId));
    const canEdit = currentUser && (currentUser.id === comment.memberId);
    const replyId = parentId ? parentId : comment.id;
    const regDate = new Date(comment.regDate).toLocaleString();
    var username = "";
    if (comment.role === "ADMIN") {
      username = "";
    } else if (comment.role === "PUBLISHER") {
      username = "UI";
    } else {
      username = comment.username.substring(0, 5) + '*'.repeat(comment.username.length - 5);
    }
  
    return (
      <div key={comment.id} className="comment">
        <div className="comment-image-container">
          <img alt="user-icon" src="/user-icon.png" />
        </div>
        <div className="comment-right-part">
          <div className="comment-content">
            <div>
              <span className="comment-author"
                style={{ color: comment.role === "ADMIN" ? "blue" : "" }}
              >{username}</span>({regDate})
            </div>
          </div>
          {!isEditing && <div className="comment-text">{comment.body}</div>}
          {isEditing && (
            <CommentInsertForm
              submitLabel="Update"
              hasCancelButton
              initialText={comment.body}
              handleSubmit={(text) => updateComment(text, comment.id, currentPage)}
              handleCancel={() => {
                setActiveComment(null);
              }}
            />
          )}
          <div className="comment-actions">
            {canReply && (
              <div
                className="comment-action"
                onClick={() =>
                  setActiveComment({ id: comment.id, type: "replying" })
                }
              >
                Reply
              </div>
            )}
            {canEdit && (
              <div
                className="comment-action"
                onClick={() =>
                  setActiveComment({ id: comment.id, type: "editing" })
                }
              >
                Edit
              </div>
            )}
            {canDelete && (
              <div
                className="comment-action"
                onClick={() => deleteComment(comment.id)}
              >
                Delete
              </div>
            )}
          </div>
          {isReplying && (
            <CommentInsertForm
              submitLabel="Reply"
              handleSubmit={(text) => addComment(text, replyId)}
            />
          )}
          {replies.length > 0 && (
            <div className="replies">
              {replies.map((reply) => (
                <Comment
                  comment={reply}
                  key={reply.id}
                  setActiveComment={setActiveComment}
                  activeComment={activeComment}
                  updateComment={updateComment}
                  deleteComment={deleteComment}
                  addComment={addComment}
                  parentId={comment.id}
                  replies={[]}
                  currentUser={currentUser}
                  currentPage={currentPage}
                />
              ))}
            </div>
          )}
        </div>
      </div>
    );
  };
  
  export default Comment;