import React from 'react';
import { call } from "../service/api-service";
import {SpeedDial, SpeedDialAction, SpeedDialIcon} from "@mui/material";

export default function BasicSpeedDial({
    setIsLoading,
    setCharacters,
    showMessage,
    setShowCharacterSortForm,
}) {
    const icons = [
        { name: '  ' },
        { name: '  ' },
        { name: '  ' },
        { name: '  ' },
    ];


    const actions = async (name) => {
        if (name === "  ") {
            setShowCharacterSortForm(true);
        }
        if (name === "  ") {
            window.location.href = "setting";
        }
        if (name === "  ") {
            setIsLoading(true);
            try {
                const response = await call("/member/characterList", "PATCH", null);
                setCharacters(response);
                setIsLoading(false);
                showMessage("  .");
            } catch (error) {
                setIsLoading(false);
                showMessage(error.errorMessage);
            }
        }
        if (name === "  ") {
            setIsLoading(true);
            try {
                const response = await call("/member/duplicate", "DELETE", null);
                setCharacters(response);
                setIsLoading(false);
                showMessage("  .");
            } catch (error) {
                setIsLoading(false);
                showMessage(error.errorMessage);
            };
        }

    }
    return (
        <>
            <SpeedDial
                ariaLabel="SpeedDial basic example"
                sx={{ position: 'fixed', top: 100, right: 20, alignItems:"end" }}
                icon={<SpeedDialIcon />}
                direction={"down"}
            >
                {icons.map((icon) => (
                    <SpeedDialAction
                        key={icon.name}
                        icon={icon.name}
                        sx={{
                            borderRadius:0,
                            display:"inline-block",
                            width:"100%",
                        }}
                        onClick={() => actions(icon.name)}
                    />
                ))}
            </SpeedDial>
        </>
    );
}