export const signup = async (mail, number, password, equalPassword) => {
    const data = {
        mail: mail,
        number: number,
        password: password,
        equalPassword: equalPassword
    }
    try {
        const response = await call("/v3/auth/signup", "POST", data);
        return response;
    } catch (error) {
        console.log(error);
        throw error;
    }
}

