import React from 'react';

//   
const Logo = ({isDarkMode}) => {
    return (
        <div className="logo"
             style={{cursor:"pointer"}}
             onClick={()=>window.location.href="/"}>
            {isDarkMode ? (
                <img alt="logo" src='/logo_white.png'/>
            ) : (
                <img alt="logo" src='/logo.png'/>
            )}
        </div>
    );
};

export default Logo;