import React from 'react';
import * as todo from '../../apis/todo';
import TodoContentWrap from '../../components/todo/TodoContentWrap';

const TodoContentWrapContainer = ({
    setIsLoading,
    showMessage,
    characters,
    setCharacters,
    setModalTitle,
    setModalContent,
    setOpenModal
}) => {
    //   /
    const updateDayContent = async (characterId, category) => {
        try {
            setIsLoading(true);
            const updatedCharacters = await Promise.all(
                characters.map(async (character) => {
                    if (character.id === characterId) {
                        try {
                            const updatedCharacter = await todo.updateDayContent(
                                character.id,
                                character.characterName,
                                category
                            );
                            return updatedCharacter;
                        } catch (error) {
                            showMessage(error.errorMessage);
                            return {
                                ...character,
                                [`${category}Check`]: 0,
                            };
                        }
                    }
                    return character;
                })
            );
            setCharacters(updatedCharacters);
        } catch (error) {
            console.error('Error updating day content:', error);
        } finally {
            setIsLoading(false);
        }
    };

    //    /
    const updateDayContentAll = async (e, characterId, category) => {
        e.preventDefault();
        try {
            setIsLoading(true);
            const updatedCharacters = await Promise.all(
                characters.map(async (character) => {
                    if (character.id === characterId) {
                        try {
                            const updatedCharacter = await todo.updateDayContentAll(
                                character.id,
                                character.characterName,
                                category
                            );
                            return updatedCharacter;
                        } catch (error) {
                            showMessage(error.errorMessage);
                            return {
                                ...character,
                                [`${category}Check`]: 0,
                            };
                        }
                    }
                    return character;
                })
            );
            setCharacters(updatedCharacters);
        } catch (error) {
            console.error('Error updating day content:', error);
        } finally {
            setIsLoading(false);
        }
    };

    //  
    const updateDayContentGuage = async (e, characterId, gaugeType) => {
        e.preventDefault();
        const newGaugeValue = window.prompt(` `);
        if (newGaugeValue !== null) {
            const parsedValue = parseInt(newGaugeValue);
            if (!isNaN(parsedValue)) {
                try {
                    setIsLoading(true);

                    const updatedCharacters = await Promise.all(
                        characters.map(async (character) => {
                            if (character.id === characterId) {
                                try {
                                    const updatedCharacter = {
                                        ...character,
                                        [`${gaugeType}Gauge`]: parsedValue,
                                    };
                                    const data = await todo.updateDayContentGuage(updatedCharacter.id,
                                        updatedCharacter.characterName, updatedCharacter.chaosGauge,
                                        updatedCharacter.guardianGauge, updatedCharacter.eponaGauge);
                                    updatedCharacter.chaosGold = data.chaosGold;
                                    updatedCharacter.guardianGold = data.guardianGold;
                                    return updatedCharacter;
                                } catch (error) {
                                    showMessage(error.errorMessage);
                                    return {
                                        ...character,
                                    };
                                }
                            }
                            return character;
                        })
                    );
                    setCharacters(updatedCharacters);
                } catch (error) {
                    console.error('Error updating day content:', error);
                } finally {
                    setIsLoading(false);
                }
            }
        }
    };

    //     
    const openDayContentAvg = (character, category) => {
        setModalTitle("" + character.characterName + " " + category + "  ");
        if (category === "") {
            var modalContent = (
                <div className="chaosVisual">
                    <span className="tip">API      .</span>
                    <p> <strong>{character.chaos.name}</strong></p>
                    <div className="flex">
                        <ul>
                            <strong>  </strong>
                            <li> <em>{character.chaos.destructionStone}</em></li>
                            <li> <em>{character.chaos.guardianStone}</em></li>
                            <li>1 <em>{character.chaos.jewelry}</em></li>
                        </ul>
                        <ul>
                            <strong>  </strong>
                            <li> <em>{character.chaos.leapStone}</em></li>
                            <li> <em>{character.chaos.shilling}</em></li>
                            <li> <em>{character.chaos.honorShard}</em></li>
                        </ul>
                    </div>
                </div>
            );
        } else {
            modalContent = (
                <div class="chaosVisual">
                    <span className="tip">API      .</span>
                    <p> <strong>{character.guardian.name}</strong></p>
                    <div className="flex one">
                        <ul>
                            <strong>  </strong>
                            <li> <em>{character.guardian.destructionStone}</em></li>
                            <li> <em>{character.guardian.guardianStone}</em></li>
                            <li> <em>{character.guardian.leapStone}</em></li>
                        </ul>
                    </div>
                </div>
            );
        }

        setModalContent(modalContent);
        setOpenModal(true);
    };


    return (
        <TodoContentWrap
            setIsLoading={setIsLoading}
            showMessage={showMessage}
            characters={characters}
            setCharacters={setCharacters}
            updateDayContent={updateDayContent}
            updateDayContentAll={updateDayContentAll}
            updateDayContentGuage={updateDayContentGuage}
            openDayContentAvg={openDayContentAvg}
            setModalTitle={setModalTitle}
            setModalContent={setModalContent}
            setOpenModal={setOpenModal}
        />
    );
};

export default TodoContentWrapContainer;