import React from 'react';
import {Link} from 'react-router-dom';
import "./Board.css";

// 
const BoardList = ({boardList}) => {
    return (
        <div className="board board--list" id="list">
            {/* <!--  --> */}
            <div className="list list--default">
                <ul>
                    {/* <!--   --> */}
                    {boardList.map((board) => (
                        <li key={board.id} className="">
                            <Link to={`/boards/${board.id}`}>
                                <div className="list__category" aria-label="">
                                    {board.notice && <span className="icon icon--notice"></span>}
                                </div>
                                <div className="list__subject" aria-label="">
                                    <span className="list__title">{board.title}</span>
                                    {/* <span className="icon icon--new"> </span> */}
                                </div>
                                <div className="list__read" aria-label="">{board.views}</div>
                                <div className="list__date"
                                     aria-label="">{new Date(board.regDate).toLocaleString()}</div>
                            </Link>
                        </li>
                    ))}
                    {/* <!-- //   --> */}
                </ul>
            </div>
            {/* <!-- //  --> */}
        </div>
    );
};

export default BoardList;