import React, {useState} from "react";
import * as auth from "../../apis/auth";
import {Link} from "react-router-dom";
import LinearIndeterminate from '../../fragments/LinearIndeterminate';
import {Button, Container, Grid, TextField, Typography} from "@mui/material";

function SignUpCharacters({message = null}) {
    const [showLinearProgress, setShowLinearProgress] = useState(false);

    const handleSubmit = async (event) => {
        try {
            setShowLinearProgress(true);
            event.preventDefault();
            //  form     .
            const data = new FormData(event.target);
            const apiKey = data.get("apiKey");
            const characterName = data.get("characterName");
            await auth.addCharacters(apiKey, characterName);
            alert(".");
            window.location.reload();

        } catch (error) {
            alert(error.errorMessage);
        } finally {
            setShowLinearProgress(false);
        }
    };


    return (
        <>
            {showLinearProgress && <LinearIndeterminate/>}
            <Container component="main" maxWidth="xs" style={{marginTop: "8%"}}>
                <form noValidate onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography component="h1" variant="h5" textAlign={"center"}>
                                  
                            </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                name="apiKey"
                                variant="outlined"
                                required
                                fullWidth
                                id="apiKey"
                                label=" ApiKey"
                            />
                            <Link to="https://canfactory.tistory.com/1081" target="_blank" variant="body2">
                                apikey ?
                            </Link>
                            <p>api ,       ! </p>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                name="characterName"
                                variant="outlined"
                                required
                                fullWidth
                                id="characterName"
                                label=""
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                            >
                                  
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Container>
        </>
    );
};

export default SignUpCharacters;
